/**
 * Áªü‰∏ÄÁöÑÊó•ÂøóÁ≥ªÁªü
 * üìù Êï¥Âêà‰∫ÜÊâÄÊúâÊó•ÂøóÂäüËÉΩÔºåÊèê‰æõÈ´òÊÄßËÉΩ„ÄÅÂèØÊâ©Â±ïÁöÑÊó•ÂøóËß£ÂÜ≥ÊñπÊ°à
 *
 * ÂêàÂπ∂‰∫Ü‰ª•‰∏ãÊñá‰ª∂Ôºö
 * - logger/logger.ts
 * - utils/logging-system.ts
 */

import type { Logger as ILogger, LogEntry, LoggerOptions, LogLevel } from '../types/logger'

// ============================================
// Á±ªÂûãÂÆö‰πâ
// ============================================

export interface LogConfig extends LoggerOptions {
  // Âü∫Á°ÄÈÖçÁΩÆ
  level?: LogLevel
  enabled?: boolean
  maxLogs?: number

  // ËæìÂá∫ÈÖçÁΩÆ
  console?: boolean
  remote?: boolean
  file?: boolean

  // ËøúÁ®ãÊó•ÂøóÈÖçÁΩÆ
  remoteUrl?: string
  remoteHeaders?: Record<string, string>
  remoteBatchSize?: number
  remoteInterval?: number

  // Ê†ºÂºèÂåñÈÖçÁΩÆ
  format?: 'json' | 'text' | 'pretty'
  timestamp?: boolean
  context?: boolean

  // ÊÄßËÉΩÈÖçÁΩÆ
  async?: boolean
  bufferSize?: number
  flushInterval?: number

  // ËøáÊª§Âô®
  filters?: Array<(entry: LogEntry) => boolean>

  // Êèí‰ª∂
  plugins?: LogPlugin[]
}

export interface LogPlugin {
  name: string
  process: (entry: LogEntry) => LogEntry | null
  flush?: () => void
}

export interface LogTransport {
  name: string
  write: (entry: LogEntry) => void | Promise<void>
  flush?: () => void | Promise<void>
}

export interface LogStats {
  total: number
  byLevel: Record<LogLevel, number>
  errors: number
  dropped: number
  buffered: number
}

// ============================================
// Áªü‰∏ÄÊó•ÂøóÁ≥ªÁªü
// ============================================

export class UnifiedLogger implements ILogger {
  private config: Required<LogConfig>
  private logs: LogEntry[] = []
  private buffer: LogEntry[] = []
  private transports: Map<string, LogTransport> = new Map()
  private plugins: LogPlugin[] = []
  private stats: LogStats
  private flushTimer?: NodeJS.Timeout
  private remoteQueue: LogEntry[] = []
  private remoteTimer?: NodeJS.Timeout

  // ÂÜÖÂ≠ò‰ºòÂåñÔºöÈôêÂà∂Êï∞ÁªÑÊúÄÂ§ßÈïøÂ∫¶
  private readonly MAX_LOGS_ABSOLUTE = 1000 // ÁªùÂØπ‰∏äÈôê
  private readonly MAX_BUFFER = 200 // ÁºìÂÜ≤Âå∫‰∏äÈôê
  private readonly MAX_REMOTE_QUEUE = 500 // ËøúÁ®ãÈòüÂàó‰∏äÈôê

  constructor(config: LogConfig = {}) {
    this.config = this.normalizeConfig(config)
    this.stats = this.initStats()

    // ÂàùÂßãÂåñ‰º†ËæìÂô®
    this.initTransports()

    // ÂàùÂßãÂåñÊèí‰ª∂ - Èò≤Âæ°ÊÄßÔºöÈôêÂà∂Êèí‰ª∂Êï∞Èáè
    if (config.plugins) {
      this.plugins = config.plugins.slice(0, 10) // ÊúÄÂ§ö10‰∏™Êèí‰ª∂
    }

    // ÂêØÂä®ÂÆöÊúüÂà∑Êñ∞
    if (this.config?.async && this.config?.flushInterval > 0) {
      this.startFlushTimer()
    }

    // ÂêØÂä®ËøúÁ®ãÊó•ÂøóÊâπÈáèÂèëÈÄÅ
    if (this.config?.remote && this.config?.remoteInterval > 0) {
      this.startRemoteTimer()
    }
  }

  /**
   * Ê†áÂáÜÂåñÈÖçÁΩÆ - ‰ºòÂåñÁâàÔºöÊõ¥‰∏•Ê†ºÁöÑÈªòËÆ§ÂÄº
   */
  private normalizeConfig(config: LogConfig): Required<LogConfig> {
    return {
      level: config.level ?? 'warn',
      enabled: config.enabled ?? true,
      maxLogs: Math.min(config.maxLogs ?? 100, this.MAX_LOGS_ABSOLUTE), // ÈôêÂà∂ÊúÄÂ§ßÊó•ÂøóÊï∞
      console: config.console ?? true,
      remote: config.remote ?? false,
      file: config.file ?? false,
      remoteUrl: config.remoteUrl ?? '',
      remoteHeaders: config.remoteHeaders ?? {},
      remoteBatchSize: Math.min(config.remoteBatchSize ?? 50, 100), // ÈôêÂà∂ÊâπÊ¨°Â§ßÂ∞è
      remoteInterval: Math.max(config.remoteInterval ?? 10000, 5000), // ÊúÄÂ∞è5ÁßíÈó¥Èöî
      format: config.format ?? 'text',
      timestamp: config.timestamp ?? false,
      context: config.context ?? false,
      async: config.async ?? false,
      bufferSize: Math.min(config.bufferSize ?? 50, this.MAX_BUFFER), // ÈôêÂà∂ÁºìÂÜ≤Âå∫Â§ßÂ∞è
      flushInterval: Math.max(config.flushInterval ?? 2000, 1000), // ÊúÄÂ∞è1ÁßíÂà∑Êñ∞
      filters: config.filters?.slice(0, 5) ?? [], // ÊúÄÂ§ö5‰∏™ËøáÊª§Âô®
      plugins: config.plugins?.slice(0, 10) ?? [], // ÊúÄÂ§ö10‰∏™Êèí‰ª∂
      showTimestamp: config.showTimestamp ?? false,
      showContext: config.showContext ?? false,
      prefix: config.prefix ?? ''
    }
  }

  /**
   * ÂàùÂßãÂåñÁªüËÆ°‰ø°ÊÅØ
   */
  private initStats(): LogStats {
    return {
      total: 0,
      byLevel: {
        debug: 0,
        info: 0,
        warn: 0,
        error: 0
      },
      errors: 0,
      dropped: 0,
      buffered: 0
    }
  }

  /**
   * ÂàùÂßãÂåñ‰º†ËæìÂô®
   */
  private initTransports(): void {
    // ÊéßÂà∂Âè∞‰º†ËæìÂô®
    if (this.config?.console) {
      this.transports.set('console', new ConsoleTransport(this.config))
    }

    // ËøúÁ®ã‰º†ËæìÂô®
    if (this.config?.remote && this.config?.remoteUrl) {
      this.transports.set('remote', new RemoteTransport(this.config))
    }

    // Êñá‰ª∂‰º†ËæìÂô®ÔºàÊµèËßàÂô®ÁéØÂ¢É‰ΩøÁî® IndexedDBÔºâ
    if (this.config?.file && typeof window !== 'undefined') {
      this.transports.set('file', new IndexedDBTransport(this.config))
    }
  }

  // ============================================
  // Ê†∏ÂøÉÊó•ÂøóÊñπÊ≥ï
  // ============================================

  debug(message: string, ...args: unknown[]): void {
    this.log('debug', message, ...args)
  }

  info(message: string, ...args: unknown[]): void {
    this.log('info', message, ...args)
  }

  warn(message: string, ...args: unknown[]): void {
    this.log('warn', message, ...args)
  }

  error(message: string, error?: unknown, ...args: unknown[]): void {
    this.log('error', message, error, ...args)
  }

  /**
   * Ê†∏ÂøÉÊó•ÂøóÊñπÊ≥ï
   */
  private log(level: LogLevel, message: string, ...args: unknown[]): void {
    if (!this.config?.enabled) return
    if (!this.shouldLog(level)) return

    // ÂàõÂª∫Êó•ÂøóÊù°ÁõÆ
    let entry: LogEntry = {
      timestamp: Date.now(),
      level,
      message,
      data: args.length > 0 ? args : undefined
    }

    // Â∫îÁî®Êèí‰ª∂
    for (const plugin of this.plugins) {
      const result = plugin.process(entry)
      if (!result) return // Êèí‰ª∂ËøáÊª§Êéâ‰∫ÜÊó•Âøó
      entry = result
    }

    // Â∫îÁî®ËøáÊª§Âô®
    for (const filter of this.config?.filters) {
      if (!filter(entry)) {
        this.stats.dropped++
        return
      }
    }

    // Êõ¥Êñ∞ÁªüËÆ°
    this.updateStats(level)

    // ÂºÇÊ≠•Ê®°ÂºèÔºöÂä†ÂÖ•ÁºìÂÜ≤Âå∫
    if (this.config?.async) {
      // Ê£ÄÊü•ÁºìÂÜ≤Âå∫Â§ßÂ∞èÈôêÂà∂
      if (this.buffer.length >= this.MAX_BUFFER) {
        // Âº∫Âà∂Âà∑Êñ∞
        this.flush()
      }

      this.buffer.push(entry)
      this.stats.buffered++

      if (this.buffer.length >= this.config?.bufferSize) {
        this.flush()
      }
    } else {
      // ÂêåÊ≠•Ê®°ÂºèÔºöÁ´ãÂç≥ÂÜôÂÖ•
      this.writeEntry(entry)
    }

    // ‰øùÂ≠òÂà∞ÂéÜÂè≤ËÆ∞ÂΩï
    this.addToHistory(entry)
  }

  /**
   * ÂÜôÂÖ•Êó•ÂøóÊù°ÁõÆ
   */
  private writeEntry(entry: LogEntry): void {
    // ÂÜôÂÖ•Âà∞ÊâÄÊúâ‰º†ËæìÂô®
    for (const transport of this.transports.values()) {
      try {
        transport.write(entry)
      } catch (error) {
        this.stats.errors++
        // ÈÅøÂÖçÈÄíÂΩí
        if (this.config?.console) {
          console.error('Logger transport error:', error)
        }
      }
    }
  }

  /**
   * Ê∑ªÂä†Âà∞ÂéÜÂè≤ËÆ∞ÂΩï - ‰ºòÂåñÁâàÔºö‰ΩøÁî®ÁéØÂΩ¢ÁºìÂÜ≤Âå∫ÈÅøÂÖçÊï∞ÁªÑÊâ©ÂÆπ
   */
  private addToHistory(entry: LogEntry): void {
    // ÂèåÈáçÈôêÂà∂ÔºöÈÖçÁΩÆÁöÑmaxLogsÂíåÁªùÂØπ‰∏äÈôê
    const effectiveMax = Math.min(this.config?.maxLogs, this.MAX_LOGS_ABSOLUTE)

    if (this.logs.length >= effectiveMax) {
      // Â∑≤Êª°ÔºöÁßªÈô§ÊúÄÊóßÁöÑÔºåÊ∑ªÂä†Êñ∞ÁöÑ
      this.logs.shift()
    }

    this.logs.push(entry)
  }

  /**
   * Ê£ÄÊü•ÊòØÂê¶Â∫îËØ•ËÆ∞ÂΩï
   */
  private shouldLog(level: LogLevel): boolean {
    const levels: LogLevel[] = ['debug', 'info', 'warn', 'error']
    const configIndex = levels.indexOf(this.config?.level)
    const levelIndex = levels.indexOf(level)
    return levelIndex >= configIndex
  }

  /**
   * Êõ¥Êñ∞ÁªüËÆ°‰ø°ÊÅØ
   */
  private updateStats(level: LogLevel): void {
    this.stats.total++
    this.stats.byLevel[level]++
  }

  // ============================================
  // ÊâπÂ§ÑÁêÜÂíåÂà∑Êñ∞
  // ============================================

  /**
   * Âà∑Êñ∞ÁºìÂÜ≤Âå∫
   */
  flush(): void {
    if (this.buffer.length === 0) return

    const entries = this.buffer.splice(0, this.buffer.length)
    this.stats.buffered = 0

    for (const entry of entries) {
      this.writeEntry(entry)
    }

    // Âà∑Êñ∞ÊâÄÊúâ‰º†ËæìÂô®
    for (const transport of this.transports.values()) {
      if (transport.flush) {
        transport.flush()
      }
    }

    // Âà∑Êñ∞Êèí‰ª∂
    for (const plugin of this.plugins) {
      if (plugin.flush) {
        plugin.flush()
      }
    }
  }

  /**
   * ÂêØÂä®ÂÆöÊúüÂà∑Êñ∞
   */
  private startFlushTimer(): void {
    this.flushTimer = setInterval(() => {
      this.flush()
    }, this.config?.flushInterval)
  }

  /**
   * ÂêØÂä®ËøúÁ®ãÊó•ÂøóÂÆöÊó∂Âô®
   */
  private startRemoteTimer(): void {
    this.remoteTimer = setInterval(() => {
      this.flushRemote()
    }, this.config?.remoteInterval)
  }

  /**
   * Âà∑Êñ∞ËøúÁ®ãÊó•Âøó - ‰ºòÂåñÁâàÔºöÈôêÂà∂ÈòüÂàóÂ§ßÂ∞èÈÅøÂÖçÊó†ÈôêÂ¢ûÈïø
   */
  private async flushRemote(): Promise<void> {
    if (this.remoteQueue.length === 0) return

    // Ê£ÄÊü•ÈòüÂàóÂ§ßÂ∞èÈôêÂà∂
    if (this.remoteQueue.length > this.MAX_REMOTE_QUEUE) {
      // ÈòüÂàóËøáÂ§ßÔºöÁßªÈô§ÊúÄÊóßÁöÑÊó•Âøó
      const excess = this.remoteQueue.length - this.MAX_REMOTE_QUEUE
      this.remoteQueue.splice(0, excess)
      this.stats.dropped += excess
    }

    const batch = this.remoteQueue.splice(0, this.config?.remoteBatchSize)

    try {
      await fetch(this.config?.remoteUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...this.config?.remoteHeaders
        },
        body: JSON.stringify(batch)
      })
    } catch {
      this.stats.errors++
      // ÊÅ¢Â§çÂ§±Ë¥•ÁöÑÊó•ÂøóÂà∞ÈòüÂàóÔºà‰ΩÜÈôêÂà∂Êï∞ÈáèÔºâ
      const toRestore = batch.slice(0, Math.min(batch.length, 50))
      this.remoteQueue.unshift(...toRestore)

      // Ë∂ÖËøáÈÉ®ÂàÜ‰∏¢ÂºÉ
      if (batch.length > toRestore.length) {
        this.stats.dropped += (batch.length - toRestore.length)
      }
    }
  }

  // ============================================
  // È´òÁ∫ßÂäüËÉΩ
  // ============================================

  /**
   * ÂàõÂª∫Â≠êÊó•ÂøóÂô®
   */
  child(context: Record<string, unknown>): UnifiedLogger {
    const childConfig = { ...this.config }
    const childLogger = new UnifiedLogger(childConfig)

    // Ê∑ªÂä†‰∏ä‰∏ãÊñáÊèí‰ª∂
    childLogger.use({
      name: 'context',
      process(entry: LogEntry): LogEntry {
        return {
          ...entry,
          context: { ...context, ...entry.context }
        }
      }
    })

    return childLogger
  }

  /**
   * ‰ΩøÁî®Êèí‰ª∂
   */
  use(plugin: LogPlugin): void {
    this.plugins.push(plugin)
  }

  /**
   * Ê∑ªÂä†‰º†ËæìÂô®
   */
  addTransport(name: string, transport: LogTransport): void {
    this.transports.set(name, transport)
  }

  /**
   * ÁßªÈô§‰º†ËæìÂô®
   */
  removeTransport(name: string): void {
    this.transports.delete(name)
  }

  /**
   * ËÆæÁΩÆÊó•ÂøóÁ∫ßÂà´
   */
  setLevel(level: LogLevel): void {
    if (this.config) {
      this.config.level = level
    }
  }

  /**
   * Ëé∑ÂèñÊó•ÂøóÁ∫ßÂà´
   */
  getLevel(): LogLevel {
    return this.config?.level
  }

  /**
   * Ëé∑ÂèñÊó•ÂøóÂéÜÂè≤
   */
  getLogs(filter?: Partial<LogEntry>): LogEntry[] {
    if (!filter) return [...this.logs]

    return this.logs.filter(log => {
      if (filter.level && log.level !== filter.level) return false
      if (filter.message && !log.message.includes(filter.message)) return false
      return true
    })
  }

  /**
   * Ê∏ÖÁ©∫Êó•Âøó
   */
  clearLogs(): void {
    // ‰ΩøÁî®Êõ¥È´òÊïàÁöÑÊñπÂºèÊ∏ÖÁ©∫Êï∞ÁªÑ
    this.logs.length = 0
    this.buffer.length = 0
    this.remoteQueue.length = 0

    // Ê∏ÖÁêÜÊèí‰ª∂ÁºìÂ≠ò
    for (const plugin of this.plugins) {
      if (typeof plugin.flush === 'function') {
        plugin.flush()
      }
    }
  }

  /**
   * Ê∏ÖÁ©∫Êó•ÂøóÔºàÂà´ÂêçÊñπÊ≥ïÔºå‰∏é Logger Êé•Âè£ÂÖºÂÆπÔºâ
   */
  clear(): void {
    this.clearLogs()
    this.resetStats()
  }

  /**
   * ËÆæÁΩÆÊúÄÂ§ßÊó•ÂøóÊï∞
   */
  setMaxLogs(max: number): void {
    if (this.config) {
      this.config.maxLogs = max
    }
    // Â¶ÇÊûúÂΩìÂâçÊó•ÂøóÊï∞Ë∂ÖËøáÊñ∞ÈôêÂà∂ÔºåÂà†Èô§ËÄÅÊó•Âøó
    while (this.logs.length > max) {
      this.logs.shift()
    }
  }

  /**
   * Ëé∑ÂèñÊúÄÂ§ßÊó•ÂøóÊï∞
   */
  getMaxLogs(): number {
    return this.config?.maxLogs
  }

  /**
   * Ëé∑ÂèñÁªüËÆ°‰ø°ÊÅØ
   */
  getStats(): LogStats {
    return { ...this.stats }
  }

  /**
   * ÈáçÁΩÆÁªüËÆ°‰ø°ÊÅØ
   */
  resetStats(): void {
    this.stats = this.initStats()
  }

  /**
   * ÈîÄÊØÅÊó•ÂøóÂô®
   */
  destroy(): void {
    // Ê∏ÖÁêÜÂÆöÊó∂Âô®
    if (this.flushTimer) {
      clearInterval(this.flushTimer)
      this.flushTimer = undefined
    }

    if (this.remoteTimer) {
      clearInterval(this.remoteTimer)
      this.remoteTimer = undefined
    }

    // ÊúÄÂêé‰∏ÄÊ¨°Âà∑Êñ∞
    this.flush()

    // Ê∏ÖÁêÜÊó•ÂøóÊï∞ÊçÆ
    this.clearLogs()

    // Ê∏ÖÁêÜ‰º†ËæìÂô®
    for (const transport of this.transports.values()) {
      if (typeof (transport as any).destroy === 'function') {
        (transport as any).destroy()
      }
    }
    this.transports.clear()

    // Ê∏ÖÁêÜÊèí‰ª∂
    for (const plugin of this.plugins) {
      if (typeof plugin.flush === 'function') {
        plugin.flush()
      }
    }
    this.plugins.length = 0
  }
}

// ============================================
// ‰º†ËæìÂô®ÂÆûÁé∞
// ============================================

/**
 * ÊéßÂà∂Âè∞‰º†ËæìÂô®
 */
class ConsoleTransport implements LogTransport {
  name = 'console'

  constructor(private config: Required<LogConfig>) { }

  write(entry: LogEntry): void {
    const { level, message, data } = entry
    const timestamp = this.config?.timestamp
      ? new Date(entry.timestamp).toISOString()
      : ''

    const prefix = this.config?.format === 'pretty'
      ? this.getPrettyPrefix(level, timestamp)
      : timestamp

    const dataArray = Array.isArray(data) ? data : []
    const args = [prefix, message, ...dataArray]

    switch (level) {
      case 'debug':

        break
      case 'info':
        console.info(...args)
        break
      case 'warn':
        console.warn(...args)
        break
      case 'error':
        console.error(...args)
        break
    }
  }

  private getPrettyPrefix(level: LogLevel, timestamp: string): string {
    const colors = {
      debug: '\x1B[36m', // Cyan
      info: '\x1B[32m', // Green
      warn: '\x1B[33m', // Yellow
      error: '\x1B[31m' // Red
    }

    const reset = '\x1B[0m'
    const color = colors[level]

    return `${color}[${level.toUpperCase()}]${reset} ${timestamp}`
  }
}

/**
 * ËøúÁ®ã‰º†ËæìÂô®
 */
class RemoteTransport implements LogTransport {
  name = 'remote'
  private queue: LogEntry[] = []
  private maxQueueSize = 10000 // Èò≤Ê≠¢ÈòüÂàóÊó†ÈôêÂ¢ûÈïø

  constructor(private config: Required<LogConfig>) { }

  write(entry: LogEntry): void {
    // Èò≤Ê≠¢ÈòüÂàóÊó†ÈôêÂ¢ûÈïøÂØºËá¥ÂÜÖÂ≠òÊ≥ÑÊºè
    if (this.queue.length >= this.maxQueueSize) {
      this.queue.shift() // ÁßªÈô§ÊúÄÊóßÁöÑÊù°ÁõÆ
    }

    this.queue.push(entry)

    if (this.queue.length >= this.config?.remoteBatchSize) {
      this.flush()
    }
  }

  async flush(): Promise<void> {
    if (this.queue.length === 0) return

    const batch = this.queue.splice(0, this.queue.length)

    try {
      await fetch(this.config?.remoteUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...this.config?.remoteHeaders
        },
        body: JSON.stringify(batch)
      })
    } catch (error) {
      // ÊÅ¢Â§çÂ§±Ë¥•ÁöÑÊó•ÂøóÔºå‰ΩÜÈôêÂà∂Êï∞Èáè
      const restoreCount = Math.min(batch.length, this.maxQueueSize - this.queue.length)
      this.queue.unshift(...batch.slice(0, restoreCount))
      throw error
    }
  }

  // Ê∑ªÂä†ÈîÄÊØÅÊñπÊ≥ï
  destroy(): void {
    this.queue.length = 0
  }
}

/**
 * IndexedDB ‰º†ËæìÂô®ÔºàÁî®‰∫éÊµèËßàÂô®ÁéØÂ¢ÉÁöÑÊñá‰ª∂Â≠òÂÇ®Ôºâ
 */
class IndexedDBTransport implements LogTransport {
  name = 'indexeddb'
  private db?: IDBDatabase
  private dbName = 'LoggerDB'
  private storeName = 'logs'
  private cleanupTimer?: NodeJS.Timeout

  constructor(private config: Required<LogConfig>) {
    this.initDB()
    // ÂÆöÊúüÊ∏ÖÁêÜÊóßÊó•Âøó
    this.cleanupTimer = setInterval(() => {
      this.cleanup()
    }, 60000) // ÊØèÂàÜÈíüÊ∏ÖÁêÜ‰∏ÄÊ¨°
  }

  private async initDB(): Promise<void> {
    const request = indexedDB.open(this.dbName, 1)

    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(this.storeName)) {
        const store = db.createObjectStore(this.storeName, {
          keyPath: 'id',
          autoIncrement: true
        })
        store.createIndex('timestamp', 'timestamp')
        store.createIndex('level', 'level')
      }
    }

    this.db = await new Promise((resolve, reject) => {
      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  }

  async write(entry: LogEntry): Promise<void> {
    if (!this.db) await this.initDB()
    if (!this.db) return

    const transaction = this.db.transaction([this.storeName], 'readwrite')
    const store = transaction.objectStore(this.storeName)
    store.add(entry)

    // Ê∏ÖÁêÜÊóßÊó•Âøó
    await this.cleanup()
  }

  private async cleanup(): Promise<void> {
    if (!this.db) return

    const transaction = this.db.transaction([this.storeName], 'readwrite')
    const store = transaction.objectStore(this.storeName)
    const count = await this.getCount(store)

    if (count > this.config?.maxLogs) {
      const deleteCount = count - this.config?.maxLogs
      const request = store.openCursor()
      let deleted = 0

      request.onsuccess = (event) => {
        const cursor = (event.target as IDBRequest).result
        if (cursor && deleted < deleteCount) {
          cursor.delete()
          deleted++
          cursor.continue()
        }
      }
    }
  }

  private getCount(store: IDBObjectStore): Promise<number> {
    return new Promise((resolve, reject) => {
      const request = store.count()
      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  }

  // Ê∑ªÂä†ÈîÄÊØÅÊñπÊ≥ï
  destroy(): void {
    if (this.cleanupTimer) {
      clearInterval(this.cleanupTimer)
      this.cleanupTimer = undefined
    }
    if (this.db) {
      this.db.close()
      this.db = undefined
    }
  }
}

